import { useState } from "react";
import "./Slider.scss";
import arrow from "./arrow.png"
import { Route ,BrowserRouter as Router, useHistory} from "react-router-dom";
import styled from "styled-components";
import img1 from "./screenshot3.png"
import img2 from "./forumscreenshot1.png"
import img3 from "./fashionscreenshot1.png"
const SliderWrapper = styled.div`
  height: 100%;
  display: flex;
  transition: all 1.5s ease;
  transform: translateX(${(props) => props.index * -100}vw);
`;
const data = [{
  id: 1,
  img:img1,
  link:"https://github.com/DemingMeng/Mapbox-App"
},
{
  id: 2,
  img: img2,
  link:"https://github.com/DemingMeng/Forum"
  
},
{
  id: 3,
  img: img3,
  link:"https://github.com/DemingMeng/Fashion-Ecommerce"
},]
const Slideritem = (props)=>{
  const history = useHistory()
  return (
    <div className="slideritem">
      
      <a href={props.item.link}>
        <img src={props.item.img} alt="" className="s-img" />
        </a>
    </div>

  )
}
const Slider = ()=>{
  
  const [index,setindex] = useState(0)
  return(
  <div className="slider">
      <img
        src={arrow}
        className="arrow-left"
        alt=""
        onClick={()=>{if(index!==0){setindex(index-1)} else{setindex(2)}}}
      />
      
      
      <img
        src={arrow}
        className="arrow-right"
        alt=""
        onClick={()=>{if(index!==2){setindex(index+1)} else{setindex(0)}}}
      />
      <SliderWrapper index = {index}>
          {data.map((item)=>{
            console.log(item)
              return (<Slideritem item = {item}></Slideritem>)
          })}
      </SliderWrapper>
  </div>)
}
export default Slider