import { Component, useState } from "react";
import "./Contact.css"
import { Email, Facebook, LinkedIn, PhoneCallback, Twitter } from "@material-ui/icons";
import { useRef } from "react";
import emailjs from "emailjs-com";
const Contact = (props)=>{
    const [success,setSuccess] = useState(false)
   
    const handleSubmit=(e) => {
            e.preventDefault();
            emailjs
              .sendForm(
                "service_0t3h1bp",
                "template_q29plr7",
                ref.current,
                "tPfGEXKhy6QufRCQZ"
              )
              .then(
                (result) => {
                 setSuccess(true)
                
                },
                (error) => {
                  console.log(error.text);
                }
              );
    };
        
    const ref = useRef()
   
    return (
         <div className="contact">
            <div className="cbackground">

            </div>
            <div className="left">
                <h1 style = {{fontSize:70}}>Contact Information</h1>
                <div className="iconbar">
                    <div className="iconwrapper">    <PhoneCallback fontSize="inherit"></PhoneCallback> +6087708332</div>
                    <div className="iconwrapper">   <Email fontSize="inherit"></Email> dmeng22@wisc.edu</div>
       
                    <div className="iconwrapper">   <LinkedIn fontSize="inherit"></LinkedIn> <Facebook fontSize="inherit"></Facebook> <Twitter fontSize="inherit"></Twitter></div>
       
            </div>
            </div> 
            <div className="right">
                <div className="ctitle">Want to know Something about me? </div>
                <sub className = "cdiscription">Tell us your thought and opinion </sub>
            <div className="form">
            <form ref={ref} onSubmit={handleSubmit}>
            <input style={{backgroundColor:"#333"}} type="text" placeholder="Name" name="from_name" />
    
            <input style={{backgroundColor:"#333"}} type="text" placeholder="Email" name="from_email" />
            <textarea style={{backgroundColor:"#333"}} rows="5" placeholder="Message" name="message" />
            <button className="projectsbutton" style = {{border:"none"}} onSubmit = {handleSubmit}>Submit</button>
            {success && "Message received, Thank you !"}
           </form>
            </div>
            </div>
         </div>
        )

}
export default Contact